@import "https://fonts.googleapis.com/css2?family=Cinzel:wght@700&family=Varta:wght@500&family=Zilla+Slab:wght@600&display=swap";
.mainPage{
    background-color: #000;
    max-height: 0rem;
}

ul{
    float: right;
    list-style-type: none;
    margin-top: 35px;
    
}

ul li{
    display: inline-block;   
    padding-right: 30px;
}

ul li a{
    font-family: sans-serif;
    text-decoration: none;
    color: #fff;
    padding: 5px;
    background-color: black;
    border: 1px solid transparent;
}

ul li a:hover{
    font-family: sans-serif;
    transition: 0.5s ease-in;
    color:#000;
    background-color: white;
}

ul li.active a {
    color:#000;
    font-family: sans-serif;
    background-color: white;
}

.behindtext{
    color: #573638;
    letter-spacing: 5px;
    font-weight: bold;
    font-family: "Cinzel";
    font-size: 80px;
    position: absolute;
    top: 35%;
    left: 50%;
    transform: translate(-50%,-50%);
}

.behindtext2{
    color: white;
    font-size: 30px;
    letter-spacing: 10px;
    position: absolute;
    top:40%;
    left:750px
}

.model{
    position: fixed;
    display: flex;
    float: left;
    margin: -15px;
    max-height: 710px;
    
}

.pagealign{
    display: flex;
    position: absolute;
    padding-top: 30px;
    max-width: 500px;
    max-height: 300px;
    left:610px;
}

.blurproverb {
    position: fixed;
    display: flex;
    opacity: 50%;
    font-family:Arial, Helvetica, sans-serif;
    font-weight: bold;
    letter-spacing: 3px;
    top: 370px;
    left: 360px;
}

