.mainPage{
    background-color: #000;
    max-height: 0rem;
}

ul{
    float: right;
    list-style-type: none;
    margin-top: 35px;
    
}

ul li{
    display: inline-block;   
    padding-right: 30px;
}

ul li a{
    font-family: sans-serif;
    text-decoration: none;
    color: #fff;
    padding: 5px;
    background-color: black;
    border: 1px solid transparent;
}

ul li a:hover{
    font-family: sans-serif;
    transition: 0.5s ease-in;
    color:#000;
    background-color: white;
}

ul li.active a {
    color:#000;
    font-family: sans-serif;
    background-color: white;
}

.behindtext{
    color: #573638;
